import { useSelector, useDispatch } from 'react-redux';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { addItem } from '../../store/slices/cart/cartSlice';
import { CartItem } from '../../store/slices/cart/types';
import { selectCartItemById } from '../../store/slices/cart/selectors';

type PizzaBlockProps = {
  id: string;
  title: string;
  price: number;
  img: string;
  sizes: [];
};

const PizzaBlock: React.FC<PizzaBlockProps> = ({
  id,
  title,
  price,
  img,
  sizes,
}) => {
  const navigation = useNavigate();
  const dispatch = useDispatch();
  const cartItem = useSelector(selectCartItemById(id));
  const count = cartItem?.count || 0;

  const types = ['—Ç–æ–Ω–∫–æ–µ', '—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ'];

  const [typeofPizza, setTypeofPizza] = useState<number>(0);
  const [sizeofPizza, setSizeofPizza] = useState<number>(0);
  const onClickAdd = () => {
    const item: CartItem = {
      title,
      id,
      price,
      img,
      type: types[typeofPizza],
      size: sizes[sizeofPizza],
      count: 0,
    };
    dispatch(addItem(item));
  };

  const onClickImage = () => {
    navigation('pizza/' + id);
  };

  return (
    <div className="pizza-block-wrapper">
      {' '}
      <div className="pizza-block">
        <img
          onClick={onClickImage}
          className="pizza-block__image"
          src={img}
          alt="Pizza"
        />
        <h4 className="pizza-block__title">{title}</h4>
        <div className="pizza-block__selector">
          <ul>
            {types.map((item, index) => {
              return (
                <li
                  key={index}
                  onClick={() => setTypeofPizza(index)}
                  className={typeofPizza === index ? 'active' : ''}
                >
                  {item}
                </li>
              );
            })}
          </ul>
          <ul>
            {sizes.map((item, index) => {
              return (
                <li
                  key={index}
                  className={sizeofPizza === index ? 'active' : ''}
                  onClick={() => setSizeofPizza(index)}
                >
                  {item}
                </li>
              );
            })}
          </ul>
        </div>
        <div className="pizza-block__bottom">
          <div className="pizza-block__price">–æ—Ç {price}‚Ç¥</div>
          <div
            className="button button--outline button--add"
            onClick={onClickAdd}
          >
            <svg
              width="12"
              height="12"
              viewBox="0 0 12 12"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                fill="white"
              />
            </svg>
            <span>–î–æ–±–∞–≤–∏—Ç—å</span>
            <i>{count}</i>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PizzaBlock;
